import { Component } from '@angular/core';
import { Platform } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { AuthcontainPage  } from '../pages/authcontain/authcontain';
import { EventcontainPage } from '../pages/eventcontain/eventcontain';
import { NotescontainPage } from '../pages/notescontain/notescontain';
import { MenuPage } from '../pages/menu/menu';
import { LibraryPage } from '../pages/library/library';

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  rootPage:any = AuthcontainPage;

  constructor(platform: Platform, statusBar: StatusBar, splashScreen: SplashScreen) {
    platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      statusBar.styleDefault();
      splashScreen.hide();
    });
  }
}



