import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import {HttpClientModule} from '@angular/common/http';//import

import { AngularFirestore } from 'angularfire2/firestore';
import { AngularFirestoreModule } from 'angularfire2/firestore';
import { AngularFireModule } from 'angularfire2';
import { FIREBASE_CONFIG } from '../providers/fire.conf';
import { AngularFireAuthModule  } from 'angularfire2/auth';
import { AngularFireStorageModule } from 'angularfire2/storage';
import { AngularFireDatabase} from 'angularfire2/database';
import { FileSizePipe } from '../pipes/file-size/file-size';

import { MyApp } from './app.component';
import { AuthProvider } from '../providers/auth/auth';
import { NotesProvider } from '../providers/notes/notes';
import { EventsProvider } from '../providers/events/events';
import { StudentsProvider } from '../providers/students/students';
import { LibraryProvider } from '../providers/library/library';

import { AuthcontainPage } from '../pages/authcontain/authcontain';
import { EventcontainPage } from '../pages/eventcontain/eventcontain';
import { FacultiesPage } from '../pages/faculties/faculties';
import { LibraryPage } from '../pages/library/library';
import { MenuPageModule } from '../pages/menu/menu.module';
import { NotescontainPage } from '../pages/notescontain/notescontain';
import { StudentsPage } from '../pages/students/students';
import { SignupPage } from '../pages/signup/signup';
import { LoginPage } from '../pages/login/login'
import { AddnotesPage } from '../pages/addnotes/addnotes';
import { AddeventPage } from '../pages/addevent/addevent';

@NgModule({
  declarations: [
    MyApp,FileSizePipe,
    AuthcontainPage, EventcontainPage, FacultiesPage, LibraryPage,
     NotescontainPage, StudentsPage, SignupPage, LoginPage, AddnotesPage, AddeventPage
  ],
  imports:[
    BrowserModule,
    IonicModule.forRoot(MyApp),
    HttpClientModule,
    AngularFireModule.initializeApp(FIREBASE_CONFIG),
    MenuPageModule,
    AngularFirestoreModule,
    AngularFireStorageModule,
    AngularFireAuthModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    AuthcontainPage, EventcontainPage, FacultiesPage, LibraryPage,
     NotescontainPage, StudentsPage, SignupPage, LoginPage, AddnotesPage, AddeventPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    AuthProvider,
    NotesProvider,
    EventsProvider,
    StudentsProvider,
    LibraryProvider
  ]
})
export class AppModule {}



