import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ToastController, AlertController, LoadingController } from 'ionic-angular';
import { EventsProvider } from '../../providers/events/events';
import { AddeventPage } from '../addevent/addevent';

@IonicPage()
@Component({
  selector: 'page-eventcontain',
  templateUrl: 'eventcontain.html',
})
export class EventcontainPage {

  eventList: any;
  user: any;
  constructor(public alCtrl: AlertController,public loadingCtrl: LoadingController,public toastCtrl: ToastController,public navCtrl: NavController, public navParams: NavParams, private eventP:EventsProvider) {
    this.eventList = [];
  }

  ionViewDidLoad() {
    this.user = this.navParams.data;
    this.user = this.navParams.get('user');
  }

  ionViewWillEnter(){
    const loading = this.loadingCtrl.create({content: 'Please wait...'});
    loading.present();
    this.eventP.getEventList().valueChanges().subscribe( list =>{
      this.eventList = list;
      loading.dismiss();
    })
  }
  deleteEvent(id){
  this.eventP.deleteAEvent(id).then( res => {
  }).catch( err =>{
  })
  }
  showAlert(header, msg) {
   const alert = this.alCtrl.create({
    title: header,
    subTitle: msg,
    buttons: ['OK']
   });
   alert.present();
  }
  gotoAddEvent(){
    this.navCtrl.push(AddeventPage);
  }
}



