import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection } from 'angularfire2/firestore';
import { AngularFireStorage } from 'angularfire2/storage';
@Injectable()
export class NotesProvider {
  filesListRef: AngularFirestoreCollection;
  constructor(private storage: AngularFireStorage,private db: AngularFirestore) {
    this.filesListRef = db.collection('files');
  }

  uploadFile(id,path: string,uploader: string, uploadTime: string,url: string, fname: string){
    const item = {
      id: id,
      path: path,
      uploader: uploader,
      date: uploadTime.toString(),
      download_url: url,
      fname: fname
    };
    return this.filesListRef.doc(id).set(item);
  }

  getFilesList() {
    return this.filesListRef;
  }
  getFileById(fileId: string) {
    return this.filesListRef.doc(fileId);
  }
  deleteFileById(path,id){
    let fileRef = this.storage.ref(id);
    return fileRef.delete().subscribe(
      () =>{
         this.filesListRef.doc(path).delete().then(()=>{
         }).catch( (err)=>{
         });
      },(err) =>{
      }
    )
  }
}



