import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection } from 'angularfire2/firestore';
import { AngularFireModule } from 'angularfire2';

@Injectable()
export class EventsProvider {
  eventListRef: AngularFirestoreCollection;
  constructor(private db: AngularFirestore) {
    this.eventListRef = db.collection('events');
  }
  createEvent(name,sdate,ehost,eloc,edec){
    const id = this.db.createId();
    const item = {
      id: id,
      name: name,
      sdate: sdate,
      ehost: ehost,
      eloc: eloc,
      edec: edec
    };
    return this.eventListRef.doc(id).set(item);
  }
  getEventList() {
    return this.eventListRef;
  }
  getEventDetail(eventId: string) {
    return this.eventListRef.doc(eventId);
  }
  deleteAEvent(id){
    return this.eventListRef.doc(id).delete();
  }
}



