import {  Component} from '@angular/core';
import {  IonicPage, NavController, NavParams, LoadingController, AlertController} from 'ionic-angular';
import { FormBuilder, FormGroup, Validators} from '@angular/forms';
import { AuthProvider } from '../../providers/auth/auth';
@IonicPage()
@Component({
  selector: 'page-signup',
  templateUrl: 'signup.html',
})
export class SignupPage {
  form_type: string;
  student_form: FormGroup;
  faculty_form: FormGroup;
  signin_toggle: boolean;
  signin_for: string;
  signup_toggle: boolean;
  signup_for: string;
  constructor(public authData: AuthProvider,public alCtrl: AlertController, public loadingCtrl: LoadingController, public navCtrl: NavController, public navParams: NavParams, public formBuilder: FormBuilder) {
   this.form_type = 'sign_up';
   this.signin_toggle = false;
   this.signin_for = 'Student';
   this.signup_toggle = false;
   this.signup_for = 'Student'
   this.student_form = this.formBuilder.group({
    'usn': ['', [
     Validators.required,
     Validators.minLength(10),
     Validators.maxLength(10),
     Validators.pattern('[0-9][a-zA-Z][a-zA-Z][0-9][0-9][a-zA-Z][a-zA-Z][0-9][0-9][0-9]')
    ]],
    'password': ['', [Validators.required, Validators.minLength(6)]]
   });
   this.faculty_form = this.formBuilder.group({
    'email': ['', [
     Validators.required,
     Validators.email
    ]],
    'password': ['', [Validators.required, Validators.minLength(6)]]
   });
  }
  signin_change(val) {
   this.signin_for = val ? 'Faculty' : 'Student';
  }
  signup_change(val) {
   this.signup_for = val ? 'Faculty' : 'Student';
  }
  get faculty_email() {
   return this.faculty_form.get('email')
  }
  get faculty_password() {
   return this.faculty_form.get('password')
  }
  get student_usn() {
   return this.student_form.get('usn')
  }
  get student_password() {
   return this.student_form.get('password')
  }
  student_signup() {
   let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });
   loading.present();
   this.authData.signupUser(this.student_usn.value + "app@app.com", this.student_password.value, "student").then(data => {
    loading.dismiss();
    this.showAlert('SignUp Success', 'You have been registered. Please Sign In to use the app.')
   }).catch(errs => {
    loading.dismiss();
    this.showAlert('SignUpFailed', errs.message);
  });
  }
  faculty_signup() {
   let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });
   loading.present();
   this.authData.signupUser(this.faculty_email.value, this.faculty_password.value, "faculty").then(data => {
    loading.dismiss();
    this.showAlert('SignUp Success', 'You have been registered. Please Sign In to use the app.')
   }).catch(errs => {
     loading.dismiss();
    this.showAlert('SignUp Failed', errs.message);
  });
  }
  student_signin() {
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });
    loading.present();
   this.authData.loginUser(this.student_usn.value + "app@app.com", this.student_password.value).then(data => {
    this.authData.getPersonRecord(data["user"]["uid"]).subscribe( data => {
      loading.dismiss();
      this.showAlert('SignIn Success', 'You have been logged in.')
      this.navCtrl.setRoot('MenuPage',{user: data});
    });
   }).catch(err => {
     loading.dismiss();
    this.showAlert('SignIn Failed', err.message);
  });
  }
  faculty_signin() {
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });
    loading.present();
   this.authData.loginUser(this.faculty_email.value, this.faculty_password.value).then(data => {
    this.authData.getPersonRecord(data["user"]["uid"]).subscribe( data => {
      loading.dismiss();
      this.showAlert('SignIn Success', 'You have been logged in.')
      this.navCtrl.setRoot('MenuPage',{user: data});
    });
   }).catch(err => {
    loading.dismiss();
    this.showAlert('SignIn Failed', err.message);
  })
  }
  showAlert(header, msg) {
   const alert = this.alCtrl.create({
    title: header,
    subTitle: msg,
    buttons: ['OK']
   });
   alert.present();
  }
}



