import { Component } from '@angular/core';
import { IonicPage, LoadingController, NavController, NavParams } from 'ionic-angular';
import { FormControl } from '@angular/forms';
import { HttpClient } from '@angular/common/http';

@IonicPage()
@Component({
  selector: 'page-faculties',
  templateUrl: 'faculties.html',
})
export class FacultiesPage {

    faculties: any;
    temp: any;
    constructor(public loadingCtrl:LoadingController,public navCtrl: NavController, public navParams: NavParams, public httpClient: HttpClient) {
      this.initializeData();
    }

    initializeData(){
      const loading = this.loadingCtrl.create({content: 'Please wait...'});
      loading.present();
      this.httpClient.get('assets/json/faculties.json').subscribe( data => {
        this.faculties = data;
        this.temp = data;
        loading.dismiss();
      })
    }

    ionViewDidLoad() {
        this.initializeData();
    }
  }



