import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { NotesProvider } from '../../providers/notes/notes';
import { FormControl } from '@angular/forms';
import { ToastController, AlertController, LoadingController } from 'ionic-angular';

import { AddnotesPage } from '../addnotes/addnotes';

@IonicPage()
@Component({
  selector: 'page-notescontain',
  templateUrl: 'notescontain.html',
})
export class NotescontainPage {

    files: any;
    files2:any;
    user: any;
    constructor(public loadingCtrl: LoadingController,public navCtrl: NavController, public navParams: NavParams, private fileh: NotesProvider) {
      this.getFilesList();
    }
    ionViewDidLoad() {
      this.user = this.navParams.get('user');
    }
    getFilesList(){
      const loading = this.loadingCtrl.create({content: 'Please wait...'});
      loading.present();
      this.fileh.getFilesList().valueChanges().subscribe( data =>{
        this.files = data;
        this.files2 = data;
        loading.dismiss();
      });
    }
    deleteFile(path,file){
        this.fileh.deleteFileById(path,file);
    }
    gotoAddNotes(){
      this.navCtrl.push(AddnotesPage);
    }
  }



