import { Component } from '@angular/core';
import { IonicPage, NavController, LoadingController, NavParams } from 'ionic-angular';
import { HttpClient } from '@angular/common/http';


@IonicPage()
@Component({
  selector: 'page-library',
  templateUrl: 'library.html',
})
export class LibraryPage {

  books: any;
  bookName: any;
  constructor(public loadingCtrl: LoadingController, public httpClient: HttpClient,public navCtrl: NavController, public navParams: NavParams) {
    this.books = [];
    this.bookName = '';
  }


  searchBooks(){
    const loading = this.loadingCtrl.create({content: 'Please wait...'});
    loading.present();
    this.httpClient.get('https://www.googleapis.com/books/v1/volumes?q='+this.bookName).subscribe( data => {
      this.books = data['items'];
      loading.dismiss();
    })
  }


    private newMethod() {
        return this.books;
    }
}



