import { Component } from '@angular/core';
import { IonicPage, NavController, LoadingController, NavParams } from 'ionic-angular';

import { StudentsProvider } from '../../providers/students/students';

@IonicPage()
@Component({
  selector: 'page-students',
  templateUrl: 'students.html',
})
export class StudentsPage {

  students: Array<any>;
  users: any;
  constructor(public loadingCtrl: LoadingController,public studP:StudentsProvider,public navCtrl: NavController, public navParams: NavParams) {
      this.students =[];
      this.loadStudents();
  }

  loadStudents() {
    const loading = this.loadingCtrl.create({content: 'Please wait...'});
    loading.present();
    this.studP.getStudentsList().valueChanges().subscribe(data =>{
      this.users = data;
      this.students = [];
      for(let i=0;i<this.users.length;i++){
        if(this.users[i]['role']=='student')
          this.students.push(this.users[i]);
      }
      loading.dismiss();
    })
  }

}



