import { Component, ViewChild } from '@angular/core';
import { App,IonicPage, NavController,Nav , NavParams } from 'ionic-angular';

import { AuthcontainPage } from '../authcontain/authcontain';
import { EventcontainPage } from '../eventcontain/eventcontain';
import { FacultiesPage } from '../faculties/faculties';
import { LibraryPage } from '../library/library';
import { NotescontainPage } from '../notescontain/notescontain';
import { StudentsPage } from '../students/students';


@IonicPage()
@Component({
  selector: 'page-menu',
  templateUrl: 'menu.html',
})
export class MenuPage {

  @ViewChild(Nav) nav: Nav;
  pages: any;
  user: any;
  rootPage = NotescontainPage;

  constructor(public app: App,public navCtrl: NavController, public navParams: NavParams) {
    this.pages = [
      { title: 'Events', component: EventcontainPage, icon: 'ios-calendar-outline'},
      { title: 'Library', component: LibraryPage, icon: 'ios-glasses-outline' },
      { title: 'Notes', component: NotescontainPage, icon: 'ios-albums-outline'},
      { title: 'Faculties', component: FacultiesPage, icon: 'ios-school-outline'},
      { title: 'Students', component: StudentsPage, icon: 'ios-ionitron-outline'},
      { title: 'Log out', component: '', icon: 'ios-power-outline'}
    ];

  }

  ionViewDidLoad() {
    this.user = this.navParams.get('user');
    this.nav.setRoot(EventcontainPage,{user:this.navParams.get('user')});
  }

  openPage(page) {
    if(page.title == 'Log out')
      this.app.getRootNavs()[0].setRoot(AuthcontainPage);
    else
      this.nav.setRoot(page.component, {user:this.navParams.get('user')});
  }

}



